<template>
  <div>
    <div style="margin-bottom:60px; margin-top:60px">
      <h1>Login</h1>
    </div>
    <v-form
      ref="form"
      class="mt-5 mb-4"
      v-model="valid"
      lazy-validation
      >
      <v-text-field
        v-model="email"
        label="Email"
        dense
        outlined
        :rules="emailRules"
        >
      </v-text-field>
      <v-text-field
        v-model="password"
        :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
        :type="show1 ? 'text' : 'password'"
        label="Contraseña"
        dense
        outlined
        @click:append="show1 = !show1"
        :rules="passwordRules"
        >
      </v-text-field>
      <div>
        <v-btn @click="loginEmail" color="accent" width="70%">Ingresar</v-btn><br>
        <v-btn @click="loginGoogle" color="error" width="70%" class="my-4">ingresar google</v-btn>
        <p>Olvidaste la contraseña? recuperala <a @click="$emit('change-mode', 'recovery')">aquí</a></p>
      </div>
      <v-divider class="my-4"></v-divider>
        <v-btn @click="register" color="accent" width="70%">Registrarse</v-btn><br>
    </v-form>
  </div>
</template>
<script>
import { getAuth, signInWithEmailAndPassword, signInWithPopup, GoogleAuthProvider } from 'firebase/auth'
export default {
  data () {
    return {
      processing: false,
      valid: true,
      email: '',
      show1: false,
      password: '',
      emailRules: [
        v => !!v || 'Email es requerido.',
        v => /.+@.+\..+/.test(v) || 'Debe ser un email valido.'
      ],
      passwordRules: [
        v => !!v || 'Contraseña es requerido.'
      ]
    }
  },
  methods: {
    async loginEmail () {
      if (this.$refs.form.validate()) {
        const correo = this.email
        const password = this.password
        try {
          const auth = getAuth()
          const userCredentials = await signInWithEmailAndPassword(auth, correo, password)
          console.log(userCredentials)
        } catch (error) {
          console.log(error.message)
        }
      }
    },
    async loginGoogle () {
      const auth = getAuth()
      const provider = new GoogleAuthProvider()
      try {
        const userCredentials = await signInWithPopup(auth, provider)
        console.log(userCredentials)
      } catch (error) {
        console.log(error.message)
      }
    },
    register () {
      this.$emit('change-mode', 'registro')
    }
  }
}
</script>

